 import {
   companytrade
 }from '../../../data/data.js'
export default function WorkCtrl() {
  var vm = this;
  vm.companytrade = companytrade;
  vm.listTitle=[
    {name: '',key : 'name', width: '20%'},
    {name: '',key : [
      {zikey:'begindate'},
      {zikey:'enddate'} ,],
      width: '30%',hasdate:true},
    {name: '',key : 'industry', width: '20%'},
    {name: '',key : 'department', width: '10%'},
    {name: '',key : 'position', width: '10%'},
  ]
}


