export default function EducationCtrl($http) {
  var vm = this;
  vm.listTitle=[
    {name: '',key : 'name', width: '20%'},
    {name: '',key : [
      {zikey:'begindate'},
      {zikey:'enddate'}
      ], width: '30%'},
    {name: '',key : 'xl', width: '20%'},
    {name: '',key : 'major', width: '20%'},
  ]

}


